* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
.main_nav {
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 5.12rem;
  display: grid;
  grid-template-columns: 5.31rem 2fr 1.5fr 2.2fr 0.25fr 5.31rem;
}
.logo {
  grid-column: 2/3;
  display: flex;
  justify-content: left;
  align-items: center;
}
.logo span {
  font-size: 2.5rem;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}
.few {
  padding-left: 2rem;
}
.work {
  color: #00ccff;
}
.search_bar {
  grid-column: 3/4;
  width: 23.75rem;
  height: 3.12rem;
  display: flex;
  margin-right: 2px;
  margin-top: 1rem;
}
.search_bar input {
  background-color: white;
  color: #000000;
  font-size: 1rem;
}
.search_bar_textarea {
  border: 2px solid #898989;
  border-radius: 5px 0 0 5px;
  width: 19.75rem;
  height: 3.12rem;
  padding-left: 1rem;
}
.search_bar button {
  width: 4rem;
  height: 3.12rem;
  border: 2px solid #898989;
  border-radius: 0 10px 10px 0;
}

.menu_list {
  grid-column: 4/5;
  margin-top: 1.8rem;
}
.menu_list ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 1rem;
  /* background-color: #00ccff; */
}

.menu_list ul li {
  list-style: none;
}
.menu_list ul li a {
  font-size: 1rem;
  text-transform: capitalize;
  color: black;
  transition: 0.5s;
  text-decoration: none;
}
.user {
  grid-row: 1/2;
  grid-column: 5/6;
  display: flex;
  align-items: center;
  justify-content: right;
}
.mobile_user_account{
  display: none;
}
/*---------------Menu Toggle Buton Display None-------------------------*/
.user a {
  display: none;
}

/*------------------------Responsive L Desktop------------------------------*/

@media (max-width: 1450px) {
  .main_nav {
    background-color: rgb(255, 255, 255);
    grid-template-columns: 2.5rem 2fr 1.5fr 2.2fr 0.25fr 2.5rem;
  }
}
/*------------------------Responsive widescreen Desktop 1920------------------------------*/

@media (max-width: 1300px) {
  .main_nav {
    background-color: rgb(255, 255, 255);
    grid-template-columns: 1rem 1.5fr 1.5fr 2.2fr 0.25fr 1rem;
  }
}
/*------------------------Responsive  Desktop 1200------------------------------*/

@media (max-width: 1200px) {
  .main_nav {
    grid-template-columns: 1rem 1.5fr 1.5fr 1fr 1rem;
    /* padding-top: 0.5rem; */
  }
  .search_bar {
    grid-column: 3/4;
    display: block;
    display: flex;
    margin-right: 2rem;
    margin-left: 2rem;
  margin-top: 1rem;
  }
  .menu_list {
    display: none;
  }
  .user {
    grid-row: 1/2;
    grid-column: 4/5;
  }
  .user a{
    display: block;
  }
  .user_icon{
    padding-right: 1rem;
  }
  
  .mobile_menu_list {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }
  .mobile_menu_list {
    background-color: #ffffff;
    height: 20rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    /* padding-left: 3rem; */
    transition: all 2s linear;
    transform-origin: right;
    /* margin-right: 1rem; */
  }
  .mobile_menu_list ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
  }
}
/*------------------------Responsive Landscape Tablet 1024------------------------------*/
@media (max-width: 1024px) {
  .main_nav {
    grid-template-columns: 1rem 1.5fr 1.5fr .25fr 1rem;
  }
  .mobile_menu_list {
    grid-column: 2/5;
  }
  .logo {
    grid-column: 2/3;
  }
  .user{
    grid-column: 4/5;
  }
  .search_bar{
    grid-column: 3/4;
  }
  .user_icon {
    display: block;
  }
 
}
/*------------------------Responsive Tablet 768------------------------------*/
@media (max-width: 768px) {
  .main_nav {
    grid-template-columns: 1rem 1fr 1fr 1rem;
  }

  .user_icon {
    display: none;
  }
  .user a {
    margin-right: 0.5rem;
  }
  .mobile_user_account{
    display: block;
  }
  .search_bar {
    display: none;
  }
  .mobile_menu_list {
    grid-column: 2/4;
  }
}

/*------------------------Responsive Mobile 480------------------------------*/
@media (max-width: 480px) {
  .main_nav {
    grid-template-columns: 1rem 1fr 1fr 1rem;
  }
  .logo {
    grid-column: 2/3;
    margin-right: 5rem;
  }

  .user a {
    grid-column: 3/4;
  }
  .mobile_menu_list {
    grid-column: 2/4;
  }
  .user_icon {
    display: none;
  }
  .search_bar {
    display: none;
  }
}
