* {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .footer_container {
    width: 100%;
    border-radius: 0px;
    margin: auto;
    /* padding-top: 2rem; */
    background-color: #4d4d4d;
    color: #ffffff;
  }

  
  h5 {
    padding-left: 2rem;
  }
  .footer_fewwork {
    text-transform: uppercase;
    font-size: 2.37rem;
    font-weight: bold;
    color: rgb(255, 255, 255);
  }
  .text_container{
     margin-top: 2rem;
      /* background-color: rgb(255, 174, 0);*/
  }
  .text_container ul {
    color: rgb(255, 255, 255);
    text-decoration: none;
  }
  .copyright {
    margin-top: 2.8rem;
    border-radius: 0;
    padding-top: 0.5rem;
    color: white;
    background-color: #4d4d4d;
    border-top: 2px solid white;
  } 
  .contact_us ul li {
    padding-top: 0.5rem;
  }
  .footer_search_bar {
    margin-left: -3rem;
      margin-top: 7rem;
    width: 14.25rem;
    height: 3.12rem;
    background-color: rgb(255, 255, 255);
    display: flex;
    border-radius: 5px;
    justify-content: center;
    /* text-align: center; */
  }
  .footer_search_bar input {
    border: 1px solid rgb(255, 255, 255);
    border-radius: 5px;
    width: 11.25rem;
    height: 3.12rem;
    padding-left: 1rem;
    font-size: 1.25rem;
  }
  .footer_search_bar button {
    border: 1px solid white;
    border-radius: 5px;
    width: 3rem;
    height: 3.12rem;
    background-color: rgb(255, 255, 255);
  }
  .logo_icon{
      margin-top: 3rem;
     
  }
  .social_media_link{
     margin-left: -7rem;
      margin-top: 4rem;
      
      
  }
  .social_media_link ul li{
      display: inline-block;
      margin-left: 4rem;
      
  }
  .social_media_link ul li img{
              width: 2rem;
              height: 2rem;
              position:absolute;
  }
  .social_media_link ul li :hover{
      width: 2.5rem;
      height: 2.5rem;
      /* position:absolute; */
  }